import React from 'react';
import Footer from '../Footer/Footer';
import Header from '../Header/Header';
import styles from './BaseLayout.module.scss';
import BgImage from '../../../assets/RealEstate.jpg';

interface BaseLayoutProps extends React.HTMLAttributes<HTMLDivElement> {}

function BaseLayout({ children, ...props }: BaseLayoutProps) {
  return (
    <div className={styles.container} {...props}>
      <Header className={styles.header} />
      <main className={styles.main}>
        <div className={styles.main__container}>{children}</div>
      </main>
      <aside className={styles.aside}>
        <img className={styles.aside__image} src={BgImage} alt="Bild" />
      </aside>
      <Footer className={styles.footer} />
    </div>
  );
}

export default BaseLayout;
